import React, { useState } from "react";
import Carousel from "react-bootstrap/Carousel";

export const TestimoniosCarusel = () => {
  const [index, setIndex] = useState<number>(0);

  const handleSelect = (selectedIndex: number) => {
    setIndex(selectedIndex);
  };
  return (
    <>
      <div
        id="TestimoniosCarusel"
        className="justify-content-center"
        data-aos="fade-up"
        data-aos-duration="3000"
      >
        <Carousel activeIndex={index} onSelect={handleSelect}>
          <Carousel.Item interval={3000}>
            <div className="card text-white text-center">
              <div className="card-body">
                <p className="card-text">
                  <i>
                    "Su tarifa es increble para personas que realmente no
                    tienen capacidad para un servicio como el que ustedes
                    prestan. Y, sin duda, su servicio para m, vale ms de lo
                    que me han cobrado."
                  </i>
                </p>
                <blockquote className="blockquote-reverse">
                  <br />
                  <footer className="footer">Fredy Alexnder Agudelo</footer>
                </blockquote>
                <br />
              </div>
              <Carousel.Caption></Carousel.Caption>
            </div>
          </Carousel.Item>
          <Carousel.Item interval={3000}>
            <div className="card text-white text-center">
              <div className="card-body">
                <p className="card-text">
                  <i>
                    "Muy buena presentacin de los servicios profesionales que
                    ofrecen, pgina amigable y clara, de mucha utilidad en el
                    da a da de las personas, ofrecen asesora inmediata y sin
                    moverse del sitio donde se encuentran, las personas no
                    gastan en desplazamientos onerosos para buscar asesora,
                    aqu la tienen a la mano."
                  </i>
                </p>
                <blockquote className="blockquote-reverse">
                  <br />
                  <footer className="footer">Gerardo Ramrez</footer>
                </blockquote>
                <br />
              </div>
              <Carousel.Caption></Carousel.Caption>
            </div>
          </Carousel.Item>
          <Carousel.Item interval={3000}>
            <div className="card text-white text-center">
              <div className="card-body">
                <p className="card-text">
                  <i>
                    "Les doy gracias infinitas por su ayuda, pues mi
                    preocupacin era mucha. Mil gracias por la eficiencia y
                    rapidez con la que ayudan a las personas."
                  </i>
                </p>
                <blockquote className="blockquote-reverse">
                  <br />
                  <footer className="footer">Albanny Giraldo</footer>
                </blockquote>
                <br />
              </div>
              <Carousel.Caption></Carousel.Caption>
            </div>
          </Carousel.Item>
          <Carousel.Item interval={3000}>
            <div className="card text-white text-center">
              <div className="card-body">
                <p className="card-text">
                  <i>
                    "Muy agradecida con ustedes y su trabajo. Sin duda,
                    cualquier cosa que necesite acudir a ustedes."
                  </i>
                </p>
                <blockquote className="blockquote-reverse">
                  <br />
                  <footer className="footer">Ana Milena Ramrez</footer>
                </blockquote>
                <br />
              </div>
              <Carousel.Caption></Carousel.Caption>
            </div>
          </Carousel.Item>
          <Carousel.Item interval={3000}>
            <div className="card text-white text-center">
              <div className="card-body">
                <p className="card-text">
                  <i>
                    "Muchsimas gracias a ustedes, fue una excelente gestin y
                    sobre todo a un excelente precio. Estoy inmensamente
                    agradecida. Yo los sper recomiendo, sin duda alguna cada
                    que tenga que acudir mi familia, mis amistades o yo, no
                    dudar en elegirlos nuevamente."
                  </i>
                </p>
                <blockquote className="blockquote-reverse">
                  <br />
                  <footer className="footer">Paula Andrea Jaramillo</footer>
                </blockquote>
                <br />
              </div>
              <Carousel.Caption></Carousel.Caption>
            </div>
          </Carousel.Item>
          <Carousel.Item interval={3000}>
            <div className="card text-white text-center">
              <div className="card-header">
                <p className=" text-center">
                  Otros comentarios que nos comparten nuestros clientes:
                </p>
              </div>
              <div className="card-body">
                <p className="card-text">
                  <i>"Excelente servicio, con total eficiencia y calidad."</i>
                  <br />
                  <i>"Es excelente el servicio para con los ciudadanos."</i>
                  <br />
                  <i>"Servicio, rpido, confiable y seguro."</i>
                  <br />
                  <i>"Un servicio excelente y gil."</i> <br />
                  <br />
                </p>
              </div>
              <Carousel.Caption></Carousel.Caption>
            </div>
          </Carousel.Item>
        </Carousel>
      </div>
    </>
  );
};
